<template>
    <div>
        <el-dialog title="消息详情"    :close-on-click-modal="false" center  :visible.sync="dialogFormVisible" :before-close="close">
            <div class="divBox">
              <div class="divc">
                <div>发送人：</div>
                <div>{{row.userName}}</div>
              </div>
              <div  class="divc">
                <div>发送单位：</div>
                <div>{{row.sendDepartment||'--'}}</div>
               </div>
              <div  class="divc">
                <div>接收时间：</div>
                <div>{{row.sendDate?moment(row.sendDate).format('YYYY-MM-DD'):'--'}}</div>
              </div>
              <div  class="divc">
                <div>要求处理时间：</div>
                <div>{{row.deadline?moment(row.deadline).format('YYYY-MM-DD'):'--'}}</div>
              </div>

              <div  class="divc">
                <div>发送内容：</div>
                <div>{{row.content||'--'}} jdhfksdf恢复考试都会发生收快递费你在哪儿我在这人</div>
              </div>

            </div>
        </el-dialog>

    </div>
</template>

<script>
    import moment from 'moment'
    export default {
        name: "copyDetails",
        components: {},
        data() {
            return {
                moment,
                row:{},
                dialogFormVisible:false,
            }
        },
        methods: {
            open(data){
                this.dialogFormVisible=true
                 this.row={...data}

            },


            close(){

                this.row={}
                this.dialogFormVisible=false
              this.$emit('getList')
            }
        },
        created() {

        },
    }
</script>

<style lang="less" scoped>
/deep/ .el-dialog{
    width: 90%;
    .el-dialog__body{
      min-height: 200px;
      padding: 15px;
        .divc{
          line-height: 30px;
          display: flex;
          justify-content: left;
          flex-flow:row;
          div:nth-child(1){
            width:120px;
          }
          div:nth-child(2){
            flex:1;
           }
        }
    }

}

</style>



