<template>
  <div class="index" ref="indexDiv">
    <div class='top_header flex justify_between'>
      <div><!--{{'<'}}--></div>
      <div>消息</div>
      <!--<div @click="saveFn('form')">保存</div>-->
		<div></div>
		<div class="iconSvg"> <icon-svg iconClass="usericon" ></icon-svg>{{userName}}{{workNumber?`(${workNumber})`:''}}</div>
    </div>
<!--    <div style="height: 68px"></div>-->
    <div class='inner'>
      <div class="banner">
            <span @click="clickButton(opt,index)" :class="isActive==index?'isActive':''" v-for="(opt,index) in buttonList" :key="opt.id">
              {{opt.name}}
            </span>
      </div>
      <div class="content">
        <SearchTable offsetTop="0" :key="isActive" :data="tableData" :tableConfig="tableConfig" :noSearch="true" ref="searchTable" refTag="searchTable"   @handleSizeChange="handleSizeChange" @handleCurrentChange="handleCurrentChange"   :showHeader="false" :showPage="true" >
          <el-table-column slot="option" label="操作" align='center' v-if="isActive==0" :width="80" >
            <template slot-scope="{ row }" >
                            <span  class="rowSvg">
                              <el-button type="primary" @click="openInfo(row)">查看</el-button>
                            </span>
            </template>
          </el-table-column>
          <el-table-column slot="option" label="操作" align='center' :width="80"  v-if="isActive==1">
            <template slot-scope="{ row }" v-if="row.state!==0">
                            <span  class="rowSvg">
                              <el-button type="primary" @click="clickAction(row)">已读</el-button>

                            </span>
            </template>
          </el-table-column>
        </SearchTable>
      </div>
    </div>
    <Info ref="Info" @getList="getList"></Info>
  </div>
</template>
<script>
	import {getUserInfo} from '@/lib/auth.js';

	import Download from '@/components/download';
  import SearchTable from '@/components/Ftable/index';
  import Info from "./info"
  import { infoConfig,warningConfig } from './tableConfig.js';
  import request from '@/lib/axios.js';
  import {  extend } from 'lodash';
  export default {
    components:{SearchTable,Download,Info},
    data(){
      return {
      	userName:getUserInfo().name,
		  workNumber:getUserInfo().workNumber,
        tableData:{},
        tableConfig:infoConfig(),
        params:{
          current: 1,
          size: 15,
        },
        isActive:0,
        airlineCompanyName:[],
        AircraftType:[],
        buttonList:[
          {name:'信息',id:'1',api:'/mms-notice/notificationRecipient/list',obj:{states:[0,1]} },
          {name:'预警',id:'2',api:'/mms-warning/warning/list',obj:{stateList:[1,2,3]} },
        ],
        buttonObj:{},
        configObj:{
          '1':infoConfig,
          '2':warningConfig,
        },
      }
    },
    created(){

      this.buttonObj=this.buttonList[0]
      this.getList()
    },
    methods:{
      openInfo(row){
        request({
          url:`${this.$ip}/mms-notice/notificationRecipient/close/${row.id}`,
          method: 'get',
          params:this.params
        }).then((d) => {
          if(d.code==200){
            this.$refs.Info.open(row)

          }
        })
       },
      clickAction(row){
        this.$confirm('此操作将该预警信息标记为已读，是否继续？','提示',{
          confirmButtonText:'确定',
          cancelButtonText:'取消'
        }).then((d)=>{
          request({
            url:`${this.$ip}/mms-warning/warning/close/${row.id}`,
            method: 'get',
          })
            .then((data) => {
              this.$message.success("已读成功！");
              this.getList();
            })
        }).catch(()=>{
          this.$message.info('已取消操作')
        })

      },
      clickButton(opt,index){
        this.isActive=index
        this.params={
          current: 1,
          size: 15,
        }
        this.buttonObj={...opt}
        this.tableConfig=this.configObj[this.buttonObj.id]()

        this.getList()
      },
      downloadFile(row){
        if(row.fileInfoList){
          this.$refs.downloadFile.open(row.fileInfoList);
        }else{
          this.$message.warning("暂无文件可以下载");
        }
      },
      getList(){
        let data=this.buttonObj.obj
        request({
          url:`${this.$ip}${this.buttonObj.api}`,
          method: 'post',
          data,
          params:this.params
        })
          .then((data) => {
            this.tableData = extend({}, this.tableData, data.data);
          })
      },
      handleSizeChange(size) {
        this.params.current = 1;
        this.params.size = size;
        this.getList()
      },
      handleCurrentChange(current) {
        this.params.current = current;
        this.getList()
      },
    },

  }
</script>
<style lang="less" scoped>
  .index {
     height:calc(100vh - 160px);
    padding: 30px;
    .banner{
      margin-bottom: 15px;
      .isActive{
        color: #fff;
        background-color: #333;
      }
      span{
        display: inline-block;
        padding: 0px 15px;
        height: 32px;
        line-height: 32px;
        text-align: center;
        color: #333;
        background-color: #fff;
        border: solid 1px #333;
        cursor: pointer;
      }
    }
    .content{
      height: calc(100% - 56px);
    }
    .top_header {
      line-height: 68px;
      height: 68px;
      position: fixed;
      font-size: 24px;
      left: 0;
      top: 0;
      right: 0;
      background: #2D4157;
      color: #fff;
      padding: 0 10px;
      z-index: 99;
    }
    .inner {
      padding-top: 68px;
      height: calc(100% - 65px) ;
      .header {
        font-size: 24px;
        text-align: center;
      }

    }
    .rowSvg{
      .el-button{
        padding:5px 10px;
      }
    }

  }
</style>



