 import moment from 'moment'
let time=(row)=>{
    if(row.sendDate){
      return moment(row.sendDate).format('YYYY-MM-DD')
    }
}
export const infoConfig = ( ) => {
    return [
        { type: 'index' , label: '序号',width:'55', align: 'center' },
        { prop: 'sendPerson', label: '发送人',width:120, align: 'center' },
        { prop: 'content', label: '发送内容', align: 'center' },
      { prop: 'sendDate', label: '接收时间',width:120, formatter: time, align: 'center'},
        // { slot: 'attachment', label: '附件',width:'70', align: 'center',sort:false},
      { slot: 'option', },
     ]
};
export const warningConfig = () => {
    return [
        { type: 'index' , label: '序号',width:'55',  align: 'center'},
        { prop: 'sendDate', label: '日期',width:120, formatter: time,align: 'center' },
        { prop: 'source', label: '来源',width:120, align: 'center' },
        { prop: 'content', label: '内容', align: 'center' },
      { slot: 'option', },
      ]
};




